<template>
  <f7-page>
    <f7-navbar title="Activit√©s" back-link="Back"></f7-navbar>
                 <div class="page-content">
                     <div class="content-block-title">Liste des Affectations</div>
                     <div class="list-block accordion-list" >
                        <ul>
                           <li class="accordion-item" v-for="todo in todos" :key="todo.title">
                              <a href="#" class="item-content item-link">
                                  <div class="item-inner">
                                    <div class="item-title">{{todo.activity_start_date}} </div>
                                    <div class="item-after"><span class="badge color-blue">{{todo.activity_start_time}}</span></div>
                                  </div>
                              </a>
                              <div class="accordion-item-content">
                                 <div class="content-block">
                                    <h2> <strong><em>{{todo.activity_site}}</em></strong>  </h2>
                                    <p>
                                      <span>{{todo.activity_start_date}}</span>
                                      <span>   </span>
                                      <span> {{todo.activity_start_time}}</span>
                                      <span> - </span>
                                      <span>{{todo.activity_end_date}}</span>
                                      <span>   </span>
                                      <span>{{todo.activity_end_time}}</span>
                                    </p>
                                    <p>
                                      <span>{{todo.activity_site_street}}</span>
                                      <span>{{todo.activity_site_city}}</span>
                                      <span>{{todo.activity_site_postal_code}}</span>
                                    </p>
                                    <p>{{todo.activity_task}}</p>
                                 </div>
                              </div>
                           </li>
                        </ul>
                     </div>
                  </div>
               </div>
            </div>
         </div>
      </div>
  </f7-page>
</template>
<script>
  export default {
    data () {
      return {
        todos: []
        // site: ''
      }
    },
    mounted () {
      // console.log(this.$device)
      // debugger
      this.todos = JSON.parse(window.sessionStorage.getItem('todos'))
    },
    components: {
      // f7Navbar,
      // f7Page,
      // f7BlockTitle
    },
    methods: {
    }
  }
</script>
<style scoped>
  .site-block {
    color: red;
  }
</style>



